@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500&display=swap);
.canvas {
  width: 100%;
  height: 100%;
}

.download {
  background-color: var(--orange);
  border-radius: 50%;
  color: var(--white);
  cursor: pointer;
  position: fixed;
  bottom: 40px;
  right: 40px;
  width: 60px;
  height: 60px;
  line-height: 70px;
  text-align: center;
}

.download i {
  font-size: 30px;
}

.preview {
    border: 2px solid var(--black);
    margin-right: 20px;
}

.form-group {
    width: 220px;
    margin-right: 20px;
}

.form-control-range {
    border-radius: 10px;
    display: block;
    width: 100%;
    margin-bottom: 10px;
}

.form-control-range {
    -webkit-appearance: none;
    outline: none;
}

.form-control-range::-webkit-slider-thumb {
    -webkit-appearance: none;
    background: var(--white);
    border-radius: 50%;
    cursor: pointer;
    height: 10px;
    width: 10px;
}

.form-control-range::-moz-range-thumb {
    -webkit-appearance: none;
    background: var(--white);
    border-radius: 50%;
    cursor: pointer;
    height: 10px;
    width: 10px;
}

.form-control-range::-ms-thumb {
    -webkit-appearance: none;
    background: var(--white);
    border-radius: 50%;
    cursor: pointer;
    height: 10px;
    width: 10px;
}

.form-control {
    display: block;
    width: 100%;
    margin-bottom: 10px;
    outline: none;
    padding: 10px;
}

.form-control option {
    cursor: pointer;
    padding: 0 4px;
}

:root {
  --white: #fff;
  --black: #000;
  --orange: hsla(33, 100%, 50%, 1);
}

* {
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
  font-size: 14px;
  margin: 0;
  padding: 0;
}

.container {
  width: 100vw;
  height: 100vh;
}

.details {
  background-color: #fff;
  background-color: var(--white);
  align-items: center;
  justify-content: center;
  display: flex;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  padding: 20px;
}

